import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {Category} from '../../../models/category.model';
import {CategoryAdminService} from '../../../services/category-admin.service';
import {DataUrl, DOC_ORIENTATION, NgxImageCompressService, UploadResponse} from 'ngx-image-compress';


@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.scss'],
})
export class AddCategoryComponent implements OnInit {

  imgResultAfterResizeMax: DataUrl = '';
  constructor(
    private categoryService: CategoryAdminService,
    private router: Router,
    private imageCompress: NgxImageCompressService,
    ) {
  }

  ngOnInit() {
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  // @ts-ignore
  // eslint-disable-next-line @typescript-eslint/member-ordering
  category: Category = {
    title: '',
    image: '',
    published: true,
  };

  uploadAndReturnWithMaxSize() {
    return this.imageCompress.uploadAndGetImageWithMaxSize(1, true).then(
      (result: DataUrl) => {
        this.imgResultAfterResizeMax = result;
        console.log('-->',result);
      },
      (result: string) => {
        console.error(
          'The compression algorithm didn\'t succed! The best size we can do is',
          this.imageCompress.byteCount(result),
          'bytes'
        );
        this.imgResultAfterResizeMax = result;
      }
    );
  }

  saveFood(): void {
    const data = {
      title: this.category.title,
      image: this.imgResultAfterResizeMax,
      published: this.category.published,
    };

    this.categoryService.create(data)
      .subscribe(
        response => {
          console.log(response);
          window.location.reload();
          //this.router.navigate(['client1/admin/listing']);

        },
        error => {
          console.log(error);
        });
  }
}
